import React, { useContext, useRef } from "react"
import { GamesContext } from '../../context/GamesState'
import "../../styles/auth.scss"
import { handleAuth } from '../../utils/auth';

// only for demo site
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserCheck } from '@fortawesome/free-solid-svg-icons';
import Credential from "./credential.js"
import "../../styles/experiences.scss"

const Auth = props => {
  const {
    onDarkMode,
    user,
    setUser,
    authError,
    setAuthError
  } = useContext(GamesContext);

  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";
  const modeClassesExp = onDarkMode ? "dark-mode-background dark-mode-outline" : "light-mode-background light-mode-outline";  // only for demo site
  const usernameRef = useRef(null);

  const registeredCredentials = (user) => {
    if (user && user.registrations) {
      return user.registrations.map((reg) => ({
        id: reg.rawId,
        type: reg.type,
        aaguid: reg.aaguid,
        flags: reg.flags,
        authenticatorAttachment: reg.authenticatorAttachment,
        attestationObject: reg.response ? JSON.stringify(reg.response.attestationObject) : [],
        transports: reg.response && reg.response.transports ? reg.response.transports : [],
        userHandle: reg.response && reg.response.userHandle ? reg.response.userHandle : [],
      }));
    } else {
      return [];
    }
  }

  const handleClick = (event) => {
    const username = usernameRef.current.value;
    if (!username) {
      setAuthError("Username is required.");
      return;
    }
    handleAuth(
      props.apiCall,
      setUser,
      setAuthError,
      registeredCredentials(user),
      username,
      props.setCookie
    );
  }

  return (
    <section className={modeClasses}>
      <div className="container">
        <div className="auth-page-layout">
          <div className="auth-container">
            <div className="words-container">
              <h2 className="subtitle">
                {registeredCredentials(user).length > 0 ? "View Passkey" : "Register Passkey"}
              </h2>
            </div>

            <div>
              <label className={`auth-label ${modeClasses}`} htmlFor="username">Username</label>
              <input className={`auth-input ${modeClasses}`} type="text" id="username" name="username" ref={usernameRef}></input>

              <div className="contact-container">
                <button
                  className={`auth-button ${modeClasses}`}
                  onClick={handleClick}
                  id="continueButton"
                  value="continue"
                >
                  <b>{registeredCredentials(user).length > 0 ? "Verify" : "Create"}</b>
                </button>

                <span className={"contact-confirmation copied"}>
                  <b>{authError}&nbsp;&nbsp;{authError && authError.includes("Verified passkey") && 
                    <FontAwesomeIcon icon={faUserCheck} className={`icon ${modeClasses}`} />}
                  </b>
                </span>
              </div>
            </div>
          </div>

          {registeredCredentials(user).length > 0 && (
            <div className="passkey-info-section">
              <div className={`passkey-info-container ${modeClassesExp}`}>
                {registeredCredentials(user).map((registration, index) => (
                  <Credential
                    key={index}
                    aaguid={registration.aaguid}
                    userPresent={registration.flags && registration.flags.userPresent ? "Yes" : "No"}
                    userVerified={registration.flags && registration.flags.userVerified ? "Yes" : "No"}
                    attachment={registration.authenticatorAttachment}
                    transports={registration.transports.toString()}
                  />
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}

export default Auth;