import React, { useContext } from "react"
import { GamesContext } from '../../context/GamesState'
import { handleLogout, handleDelete } from '../../utils/auth';
import "../../styles/navbar.scss"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const NavBar = props =>  {
  const { onDarkMode, setUser, user, setAuthError } = useContext(GamesContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";

  const handleDeleteClick = (event) => {
    handleDelete(props.apiCall, setUser, setAuthError, props.cookies, props.removeCookie);
  }

  const handleLogoutClick = (event) => {
    handleLogout(props.apiCall, setUser, setAuthError, props.cookies, props.removeCookie);
  }

  return (
    <div className="nav-bar">
      <h5 className="nav-title">Passkey <b>Demo</b></h5>

      <div>
        {user && <button
          className={`auth-button delete-button ${modeClasses}`}
          onClick={handleDeleteClick}
          id="deleteButton"
          value="delete"
        >
          <b>Delete</b>
        </button>}
        &nbsp;&nbsp;
        {false && <button
          className={`auth-button ${modeClasses}`}
          onClick={handleLogoutClick}
          id="logoutButton"
          value="logout"
        >
          <b>Logout</b>
        </button>}
      </div>
    </div>
  );
}

export default NavBar;