import React, { useState, useEffect, useContext } from 'react';
import "../../styles/auth.scss"
import { getAAGUIDInfo } from '../../utils/aaguid';
import { GamesContext } from '../../context/GamesState';

const Credential = ({ aaguid, userPresent, userVerified, attachment, transports }) => {
  const [aaguidInfo, setAAGUIDInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const { onDarkMode } = useContext(GamesContext);

  // Property descriptions based on WebAuthn spec
  const propertyDescriptions = {
    aaguid: "Authenticator Attestation Globally Unique ID - Identifies the type and manufacturer of the security key or authenticator",
    userPresent: "Indicates if the user was physically present during authentication (e.g., by pressing a button)",
    userVerified: "Confirms if the user was verified by the authenticator (e.g., via fingerprint, PIN, etc.)",
    attachment: "How the authenticator is connected to the device - can be 'platform' (built-in) or 'cross-platform' (external)",
    transports: "The ways the authenticator can communicate (USB, NFC, Bluetooth, or internal)"
  };

  useEffect(() => {
    async function fetchAAGUIDInfo() {
      if (aaguid) {
        const info = await getAAGUIDInfo(aaguid);
        setAAGUIDInfo(info);
      }
      setLoading(false);
    }

    fetchAAGUIDInfo();
  }, [aaguid]);

  const getIconUrl = () => {
    if (!aaguidInfo) return null;
    return onDarkMode ? aaguidInfo.icon_dark : aaguidInfo.icon_light;
  };

  return (
    <div className="credential-info">
      <div className="credential-header">
        {!loading && aaguidInfo && getIconUrl() && (
          <img 
            src={getIconUrl()} 
            alt={`${aaguidInfo.name} logo`}
            className="credential-icon"
          />
        )}
        <p className="provider-name">
          <strong>Provider</strong>
          {loading ? "Loading..." : (aaguidInfo?.name || "Unknown")}
        </p>
      </div>
      
      <div className="credential-details">
        <p className="detail-item">
          <strong>AAGUID</strong>
          <span>{aaguid}</span>
          <div className="tooltip">{propertyDescriptions.aaguid}</div>
        </p>
        <p className="detail-item">
          <strong>User Present</strong>
          <span>{userPresent}</span>
          <div className="tooltip">{propertyDescriptions.userPresent}</div>
        </p>
        <p className="detail-item">
          <strong>User Verified</strong>
          <span>{userVerified}</span>
          <div className="tooltip">{propertyDescriptions.userVerified}</div>
        </p>
        <p className="detail-item">
          <strong>Attachment</strong>
          <span>{attachment}</span>
          <div className="tooltip">{propertyDescriptions.attachment}</div>
        </p>
        <p className="detail-item">
          <strong>Transports</strong>
          <span>{transports}</span>
          <div className="tooltip">{propertyDescriptions.transports}</div>
        </p>
      </div>
    </div>
  );
};

export default Credential;
