import React, { useContext, useEffect } from "react"
import { useCookies } from 'react-cookie';
import { GamesContext } from '../../context/GamesState'
import { useApiCall, handleSplash } from '../../utils/auth';

import NavBar from "./navbar"
import Auth from "../auth/auth";

const Splash = props => {
  const {
    onDarkMode,
    user,
    setUser,
    authError,
    setAuthError
  } = useContext(GamesContext);
  const [cookies, setCookie, removeCookie] = useCookies(['id', 'token']);
  console.log("User: ", user);
  console.log("Cookies: ", cookies);

  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";

  const apiCall = useApiCall(cookies);

  useEffect(() => {
    handleSplash(apiCall, setUser, cookies, setCookie);
  }, [cookies]);

  return (
    <section className={`${modeClasses} section-container`}>
      <NavBar apiCall={apiCall} cookies={cookies} removeCookie={removeCookie} />
      { user && false ?  // change after demo site
        <div className="container landing-container">
          <div className="words-container">
            <h2 className="subtitle">
              Lobby
            </h2>
          </div>
          <div>Hello, {user.email}</div>
        </div> :
        <Auth apiCall={apiCall} cookies={cookies} setCookie={setCookie} />
      }
    </section>
  );
}

export default Splash;